/*
 * Color palette: Asphalt
 */

.bg-asphalt {
	background-color: #2c3e50;
}
.border-left-asphalt {
	border-left: 0.25rem solid #2c3e50 !important;
}
.border-top-asphalt {
	border-top: 0.25rem solid #2c3e50 !important;
}
.border-right-asphalt {
	border-right: 0.25rem solid #2c3e50 !important;
}
.border-bottom-asphalt {
	border-bottom: 0.25rem solid #2c3e50 !important;
}
.text-asphalt {
	color: #2c3e50 !important;
}

/*
 * Color palette: Dark Black
 */

.bg-dark-black {
	background-color: #1a1a1a;
}

/*
 * Color palette: Perform
 */

.bg-perform {
