/*
 * Color palette: Midnight
 */

.bg-midnight {
	background-color: #40739e;
}
.bg-gradient-midnight {
	background-color: #40739e;
	background-image: linear-gradient(to bottom, #40739e, #33648e, #26557f, #19466f, #083860);
	background-size: cover;
}
.border-left-midnight {
	border-left: 0.25rem solid #40739e !important;
}
.border-top-midnight {
	border-top: 0.25rem solid #40739e !important;
}
.border-right-midnight {
	border-right: 0.25rem solid #40739e !important;
}
.border-bottom-midnight {
	border-bottom: 0.25rem solid #40739e !important;
}
.text-midnight {
	color: #40739e !important;
}

.disable-select {
	-webkit-touch-callout: none;
	/* iOS Safari */
	-webkit-user-select: none;
	/* Safari */
	-khtml-user-select: none;
	/* Konqueror HTML */
	-moz-user-select: none;
	/* Firefox */
	-ms-user-select: none;
	/* Internet Explorer/Edge */
	user-select: none;
	/* Non-prefixed version, currently supported by Chrome and Opera */
}

.bg-square-asphalt {
	height: 100%;
	color: rgb(44, 62 ,80);
	background:
		linear-gradient(-135deg, rgb(44, 62, 80) 10%, transparent),
		repeating-linear-gradient(45deg, rgba(44, 62, 80, 1) 0%, rgba(31, 48, 94, 0.6) 5%, transparent 5%, transparent 10%),
		repeating-linear-gradient(-45deg, rgba(44, 62, 80, 0.5) 0%, rgba(31, 48, 94, 0.5) 5%, transparent 5%, transparent 10%);
	background-color: rgba(44, 62, 80, 0.75);
}

.bg-square-midnight {
	height: 100%;
	color: rgb(64, 115, 158);
	background:
		linear-gradient(-135deg, rgb(64, 115, 158) 10%, transparent),
		repeating-linear-gradient(45deg, rgba(64, 115, 158, 1) 0%, rgba(31, 48, 94, 0.6) 5%, transparent 5%, transparent 10%),
		repeating-linear-gradient(-45deg, rgba(64, 115, 158, 0.5) 0%, rgba(31, 48, 94, 0.5) 5%, transparent 5%, transparent 10%);
	background-color: rgba(64, 115, 158, 0.75);
}

.bg-texture {
	color: white;
  	background-color:#66a3ff;
 	background-image: repeating-linear-gradient(transparent, transparent 50px, purple 50px, purple 53px, transparent 53px, transparent 63px, purple 63px, purple 66px, transparent 66px, transparent 116px, rgba(0,0,0,.5) 116px, rgba(0,0,0,.5) 166px, rgba(255,255,255,.2) 166px, rgba(255,255,255,.2) 169px, rgba(0,0,0,.5) 169px, rgba(0,0,0,.5) 179px, rgba(255,255,255,.2) 179px, rgba(255,255,255,.2) 182px, rgba(0,0,0,.5) 182px, rgba(0,0,0,.5) 232px, transparent 232px),  
		repeating-linear-gradient(270deg, transparent, transparent 50px, purple 50px, purple 53px, transparent 53px, transparent 63px, purple 63px, purple 66px, transparent 66px, transparent 116px, rgba(0,0,0,.5) 116px, rgba(0,0,0,.5) 166px, rgba(255,255,255,.2) 166px, rgba(255,255,255,.2) 169px, rgba(0,0,0,.5) 169px, rgba(0,0,0,.5) 179px, rgba(255,255,255,.2) 179px, rgba(255,255,255,.2) 182px, rgba(0,0,0,.5) 182px, rgba(0,0,0,.5) 232px, transparent 232px);
  
}

/*
 * Table
 */

table {
    counter-reset: rowIndex;
}
table tr td:first-child::before {
	counter-increment: rowIndex;
	content: counter(rowIndex) ".";
}
table td {
	text-align: left;
	vertical-align: middle !important;
}
table th {
	text-align: left;
	vertical-align: middle !important;
}